import Vue from 'vue'
import Vuex from 'vuex'
import { mutations, state } from './mutations'
import getters from './getters'
import home from './modules/home'

Vue.use(Vuex)

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  modules: {
    home
  }
})

export default store



